.message{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    align-items: flex-start;
}

.messageTop{
    display: flex;
}

.messageImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}

.messageText{
    padding-left: 8px;
    padding-right: 8px;
    border-radius: 10px;
    background-color:black;
    color:white;
    max-width: 300px;
}

.messageButton{
    font-size: 12px;
    margin-top: 10px;
}

.message.own{
    align-items: flex-end;
}

.message.own .messageText{
    background-color: rgb(209, 209, 209);
    color: black;
}