}

.hero-text .show {
    text-decoration: none;
    background: #222;
    padding: 1rem 2rem;
    border-radius: 6px;
    font-size: 1.1rem;
    font-weight: bold;
    letter-spacing: 2px;
    color: #fff;
}

.hero-text .show:hover{
    background: #fff;
    color: #222;
}

.hero-text .hide {
    display: none;
}


