.NavbarItems {
    background-color: rgb(255, 255, 255);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    box-shadow: 0 5px 15px 0 rgb(0, 0, 0, .25);
    width: 95%;
    height: 60px;
    border-radius: 10px;
    position: fixed;
    top: 10px;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    z-index: 9990;
}

.active {
    background: #fff !important;
}

.nav-menu ul {
    margin-top: 10px !important;
}

.navbar-logo {
    color: rgb(34, 2, 71);
    cursor: pointer;
    font-weight: 900;
    font-size: xx-large;
    font-family: Arial, Helvetica, sans-serif;
    /* color: rgb(31, 28, 32); */
    /* text-shadow: 1px 2px #afa9aa; */
    letter-spacing: 2px;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(7, auto);
    grid-gap: 10px;
    list-style: none;
    align-items: center;
    padding-top: 15px;
}

.nav-links {
    text-decoration: none;
    color: #222;
    font-size: 1rem;
    font-weight: 600;
    padding: 0.7rem 1rem;
    white-space: nowrap;
}

.nav-links i {
    padding-right: 10px;
}

.nav-links:hover {
    background-color: #01959a;
    color: #fff;
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
}

.nav-links-mobile {
    display: block;
    text-align: center;
    padding: .1rem;
    margin: auto;
    padding-left: 5px;
    padding-right: 5px;
    border-radius: 4px;
    width: 100%;
    background: #070707;
    color: #fff;
    font-size: .8rem;
    font-weight: 500;
    letter-spacing: 1px;
}

.nav-links-mobile:hover {
    background-color: white;
    color: black;
}

.button {
    padding: 0.5rem;
    white-space: nowrap;
    border-radius: 0.3rem;
    font-size: 600;
    border: none;
    cursor: pointer;
    transition: 0.2s ease-in-out;
}

.button:hover {
    background-color: #151516;
    color: black;
}

