.sidebar {
  display: flex;
  flex-direction: column;
  position: fixed;
  height: 100vh;
  padding-top: 4rem;
  transition: all 300ms ease;
  background: rgb(3, 207, 221);
  border-start-start-radius: 20px;
  border-bottom-left-radius: 20px;
}

/* logo */
.bars {
  display: none;
}

.logo {
  display: flex;
  height: 5rem;
  font-weight: bold;
  font-size: 22px;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
  justify-content: center;
  height: 4%;
}

.logo>span {
  color:black;
}

.logo>img {
  width: 3rem;
  height: 3rem;
}

/* menu */
.menu {
  margin-top: 4rem;
  display: flex;
  flex-direction: column;
  grid-gap: 2rem;
  gap: 2rem;
}

.menuItem {
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  height: 2.5rem;
  margin-left: 2rem;
  position: relative;
  transition: all 300ms ease;
  border-radius: 0.7rem;
  font-size: 14px;
}

.menuItem:hover {
  cursor: pointer;
}

.menu .menuItem:last-child {
  position: absolute;
  bottom: 2.3rem;
  width: 100%;
}

.menuItem-active {
  background: rgb(126, 147, 158);
  margin-left: 0;
}

.menuItem-active::before {
  content: "";
  width: 8px;
  height: 100%;
  background: rgb(49, 175, 248);
  margin-right: calc(.5rem - 4px);
}

/* Tablets */
@media screen and (max-width: 1200px) {
  .menuItem>span {
    display: none;
  }

  .logo {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  .sidebar {
    position: absolute;
    /* z-index: 9; */
    background: rgb(49, 175, 248);
    width: 55%;
    padding-right: 1rem;
    height: 100%;
    display: none;
    position: absolute;
  }

  .menuItem>span {
    display: block;
  }

  .logo {
    display: flex;
  }

  .menu .menuItem:last-child {
    position: relative;
    margin-top: 6rem;
  }

  .bars {
    display: flex;
    position: fixed;
    top: 2rem;
    left: 60%;
    background: #ffe0e0;
    padding: 10px;
    border-radius: 10px;
    z-index: 9;
  }

  .close {
    left: -100%;
  }
}
