.chatMenuWrapper,
.chatBoxWrapper {
    padding: 10px;
    height: 100%;
}

.noConversationText {
    position: absolute;
    top: 30%;
    font-size: 30px;
    text-align: center;
    cursor: default;
}
.conversation{
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
    margin-top: 15px;
}
.conversation:hover{
    background-color: rgb(236, 234, 234);
}

.conversationImg{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 20px;
}

.conversationName{
    font-weight: 700;
    color: black;
}
.chatBoxBottom {
    position: relative;
  }
  
  .chatMessageInputContainer {
    position: relative;
  }
  
  .chatMessageInput {
    height: 40px;
    width: calc(100% - 100px);
    padding: 10px;
    font-size: 16px;
    border-radius: 20px;
    border: none;
    outline: none;
  }
  
  .emojiPicker {
    position: absolute;
    bottom: 40px;
    right: 0;
    z-index: 99;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
  }
