.container {
    overflow: hidden;
    margin: 45px auto;
    background: linear-gradient(90deg, rgba(211, 217, 225, 1) 19%, rgba(91, 167, 153, 1) 50%, rgba(211, 217, 225, 1) 76%);
    box-shadow: 1px 6px 60px 40px rgba(192, 201, 200, 0.8);
}

.heading{
    padding-top:2rem ;
    font-weight:900;
    font-size: xx-large;
    font-family: Arial, Helvetica, sans-serif;
    color: #152c47;
    text-shadow: 1px 2px #685356;
    letter-spacing:4px;
}

.body {
    position: relative;
    width: 920px;
    height: 410px;
    margin: 10px auto;
    border: 1px solid #dddd;
    border-radius: 18px;
    overflow: hidden;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.box-1 img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}

.box-2 {
    padding: 10px;
}

.box-1,
.box-2 {
    width: 50%;
}

.h-1 {
    font-size: 24px;
    font-weight: 700;
}


.container .box {
    width: 100px;
    height: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 2px solid transparent;
    text-decoration: none;
    color: #615f5fdd;
}

.box:active,
.box:visited {
    border: 2px solid #ee82ee;
}

.box:hover {
    border: 2px solid #ee82ee;
}


.footer .p-color {
    color: #ee82ee;
}

.footer.text-muted {
    font-size: 10px;
}

.fas.fa-times {
    position: absolute;
    top: 20px;
    right: 20px;
    height: 20px;
    width: 20px;
    background-color: #f3cff379;
    font-size: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.fas.fa-times:hover {
    color: #ff0000;
}

.btnhover {
    color: orange;
}

.btnhover:hover {
    color: blue;
}


@media (max-width:767px) {
    body {
        padding: 10px;
    }

    .body {
        width: 100%;
        height: 100%;
    }

    .box-1 {
        width: 100%;
    }

    .box-2 {
        width: 100%;
        height: 440px;
    }
    .box-1 img {
        display: none;
    }
    
}